import axios from 'axios'

const service = axios.create({
  baseURL: process.env.NODE_ENV === 'production' || process.env.NODE_ENV === 'build' ? './' : process.env.VUE_APP_BASE_API,
  headers: { "ContentType":"application/json", "Accept":"application/json","x-odiuqil-caller":"vjGFMxvaa3k7FukD85szrUF95GgW7AnNJW3bSDjG8aVSUtbN" },
  timeout: 5000
})

service.interceptors.request.use(
  config => {
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

service.interceptors.response.use(
  response => {
    const res = response.data
    if (!res.code) {
      return Promise.reject(new Error(res.message || 'Error'))
    } else {
      return res
    }
  },
  error => {
    return Promise.reject(error)
  }
)

export default service
