import Vue from 'vue'
import App from './App.vue'
import router from './router'
import '@/utils/lib-flexible.js'
import FastClick from 'fastclick'
import '@/assets/icon/iconfont.css'
import '@/assets/icon/iconfont.js'

import { Picker, ActionSheet } from 'vant'
Vue.use(Picker)
Vue.use(ActionSheet)
import 'vant/lib/index.less'




// const LANGUAGES = {
//   ch: require('./assets/languages/ch.json'),
//   en: require('./assets/languages/en.json')
// }
let forCheckLanguage = navigator.language || navigator.browserLanguage
localStorage.setItem('LANGUAGES', forCheckLanguage ? forCheckLanguage.toLowerCase() : 'en')




FastClick.attach(document.body)
// 解决 ios 多次点击才可对焦的问题
// 如果页面上有一些特定的元素不需要使用fastclick来立刻触发点击事件，可以在元素的class上添加 needsclick
let deviceIsWindowsPhone = navigator.userAgent.indexOf("Windows Phone") >= 0
let deviceIsIOS = /iP(ad|hone|od)/.test(navigator.userAgent) && !deviceIsWindowsPhone

FastClick.prototype.focus = function(targetElement) {
  let length

  // Issue #160: on iOS 7, some input elements (e.g. date datetime month) throw a vague TypeError on setSelectionRange. These elements don't have an integer value for the selectionStart and selectionEnd properties, but unfortunately that can't be used for detection because accessing the properties also throws a TypeError. Just check the type instead. Filed as Apple bug #15122724.
  if (deviceIsIOS && targetElement.setSelectionRange && targetElement.type.indexOf('date') !== 0 && targetElement.type !== 'time' && targetElement.type !== 'month' && targetElement.type !== 'email') {
    length = targetElement.value.length
    targetElement.focus()
    targetElement.setSelectionRange(length, length)
  } else {
    targetElement.focus()
  }
}



import VueAnalytics from 'vue-analytics'


Vue.use(VueAnalytics, {
  id: 'G-PSZNECXPMV',
  router,
  disableScriptLoader: true,
  autoTracking: {
    pageviewOnLoad: false
  }
})

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
