        <p>A Liquido desenvolveu este serviço de pagamento (o "Serviço") para facilitar aos comerciantes (clientes) a aceitação de uma grande variedade de métodos de pagamento locais preferidos em muitos países. Ao usar o Serviço, você concorda com nossos termos de uso, conforme estabelecido abaixo.</p>
        <h1>1. Nossos serviços</h1>
