        <h1>7. Nossa Segurança</h1>
        <p>A Liquido é responsável por proteger a segurança dos Dados em nossa posse e manterá procedimentos administrativos, técnicos e físicos comercialmente razoáveis para proteger todas as informações pessoais sobre você e seus clientes armazenadas em nossos servidores contra acesso não autorizado, perda ou modificação acidental. No entanto, não podemos garantir que terceiros não autorizados nunca sejam capazes de anular essas medidas ou usar essas informações pessoais para fins impróprios. Você reconhece que fornece essas informações pessoais sobre você e seus clientes por sua conta e risco. Recomendamos que você revise nossa Política de Privacidade, que o ajudará a entender como coletamos, usamos e protegemos as informações que você nos fornece.</p>
