        <h1>11. Uso Restrito</h1>
        <p>Você é obrigado a obedecer a todas as leis, regras e regulamentos aplicáveis ao seu uso do Serviço (por exemplo, aqueles que regem serviços financeiros, proteção ao consumidor, concorrência desleal, antidiscriminação ou propaganda enganosa). Além de quaisquer outros requisitos ou restrições estabelecidos neste Contrato, você não deve: (i) utilizar as opções de pagamento como forma de adiantar dinheiro aos usuários por meio de métodos de pagamento, (ii) enviar quaisquer transações de pagamento para processamento que não surgiram de sua venda de bens ou serviços a um cliente comprador, ou aceitação de uma doação de caridade de boa-fé, (iii) atuar como intermediário de pagamento ou agregador ou revender nossos serviços em nome de terceiros, (iv) enviar o que você acredita como autorizações potencialmente fraudulentas ou transações de pagamento fraudulentas, ou (v) usar a Liquido de forma que nossos parceiros de pagamento acreditem que você está abusando dos serviços da Liquido e violar as regras aplicáveis. Você também concorda em não permitir que terceiros façam o seguinte: (i) acessar ou tentar acessar sistemas, programas ou dados da Liquido que não sejam disponibilizados para uso público; (ii) copiar, reproduzir, republicar, fazer upload, postar, transmitir, revender ou distribuir de qualquer forma material da Liquido; (iii) permitir que qualquer terceiro use e se beneficie do Serviço por meio de aluguel, arrendamento, compartilhamento de tempo, agência de serviços ou outro acordo; (iv) transferir quaisquer direitos concedidos a você sob este Contrato; (v) contornar qualquer uma das limitações técnicas do Serviço, usar qualquer ferramenta para habilitar recursos ou funcionalidades que estejam de outra forma desabilitados no Serviço, ou descompilar, desmontar ou fazer engenharia reversa do Serviço, exceto na medida em que tal restrição seja expressamente proibido por lei; (vi) realizar ou tentar realizar quaisquer ações que possam interferir no funcionamento adequado do Serviço, impedir o acesso ou uso do Serviço por nossos outros usuários ou impor uma carga não razoável ou desproporcionalmente grande em nossa infraestrutura; ou (vii) usar o Serviço de outra forma, exceto conforme expressamente permitido nesta seção.</p>
        <h1>12. Suspeita de Uso Não Autorizado ou Ilegal</h1>
        <p>Reservamo-nos o direito de não autorizar ou liquidar qualquer transação que você enviar que acreditemos violar este Contrato, qualquer outro contrato da Liquido, ou expor você, outros usuários da Liquido, nossos processadores ou a Liquido a danos, incluindo, mas não limitado a fraude e outros atos criminosos. Você está nos concedendo autorização para compartilhar informações com as autoridades policiais sobre você, suas transações e sua conta de serviço da Liquido se eles solicitarem por meio de uma ordem judicial ou se forem consideradas ilegais pelo governo de Cingapura.</p>
        <h1>13. Divulgações e Avisos</h1>
