        <p>Se você receber informações sobre outras pessoas, incluindo Portadores de Cartão, por meio do uso do Serviço, deverá manter essas informações confidenciais e usá-las apenas em conexão com o Serviço. Você não pode divulgar ou distribuir tais informações a terceiros ou usar tais informações para fins de marketing, a menos que receba o consentimento expresso do usuário para fazê-lo. Você não pode divulgar informações de pagamento a terceiros, exceto em relação ao processamento de um pagamento para seus usuários neste Serviço.</p>
