        <p>Em nenhum caso seremos responsáveis por qualquer perda ou dano, incluindo, sem limitação, perda ou dano indireto ou conseqüente, ou qualquer perda ou dano decorrente de perda de dados ou lucros decorrentes ou em conexão com o uso deste site.</p>
