import zh from '@/assets/languages/zh.json'
import en from '@/assets/languages/en.json'

const LANGUAGES = {
    'zh': zh,
    'en': en
}

/**
 * @method
 * @desc 获取当前语言类型(默认英文)
 * @param {object} instance 当前 this 实例
 * @return {string} 当前语言类型
 */
 function getCurrentLanguage (instance) {
    // 默认为英文
    let currentLanguage = 'en'
    try {
        // 判断当前客户端或 localstorage 保存语言是否存在
        let temp = localStorage.getItem('LANGUAGES')
        Object.keys(LANGUAGES).some(item => {
            if (temp.indexOf(item) > -1) {
                // 语言存在
                currentLanguage = item
                return true
            }
        })
    } catch (err) {}
    return currentLanguage
    // return ENGLISH
}

/**
 * @method
 * @desc 获取当前语言类型对应的文本内容
 * @param {object} instance 当前 this 实例
 * @return {json} 对应文本内容
 */
function getTextContent (instance) {
    let temp = getCurrentLanguage(instance)

    // 返回语言对应的 json 格式文本内容
    return LANGUAGES[temp]
}

export default {
    getCurrentLanguage,
    getTextContent
}